.burger-menu {
  display: flex;
  justify-content: flex-end;
  height: 100%;
  width: 100%;

  box-sizing: border-box;
  background: rgba(0, 0, 0, 0.3);

  margin: 0;
  padding: 0;

  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
}

.burger-menu__container {
  max-width: 520px;
  min-width: 320px;
  background: #fafafa;

  margin: 0;
  padding: 159px 120px 90px 98px;
  opacity: 1;
}

.burger-menu__close {
  background-image: url(../../images/close.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  opacity: 1;
  width: 32px;
  height: 32px;
  border: none;
  cursor: pointer;
  transition: opacity 0.2s ease-in-out;
  background-color: transparent;

  margin: 0;
  padding: 0;

  position: absolute;
  top: 22px;
  right: 40px;
}

.burger-menu__close:hover {
  opacity: 0.8;
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;

  margin: 0;
  padding: 0;
}

.menu__links {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 22px;
  box-sizing: border-box;

  margin: 0;
  padding: 0;
}

.menu__link {
  text-decoration: none;
  cursor: pointer;
  display: block;

  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #000000;
  box-sizing: border-box;
  min-width: 50px;

  margin: 0;
  padding: 0 0 6px;

  transition: opacity 0.2s ease-in-out;
}

.menu__link:hover {
  opacity: 0.5;
}

.menu__link_active {
  border-bottom: 1px solid #000;
  cursor: default;
  opacity: 1;
}

.menu__link_active:hover {
  cursor: default;
  opacity: 1;
}

.menu__button {
  text-decoration: none;
  cursor: pointer;

  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #000000;

  background: #f4f4f4;
  border-radius: 20px;

  padding: 8px 20px;
  margin: 150px 0 0;

  transition: opacity 0.2s ease-in-out;
}

.menu__button:hover {
  opacity: 0.8;
}

@media screen and (max-width: 520px) {
  .burger-menu__container {
    padding: 144px 0 46px;
  }

  .burger-menu__close {
    top: 13px;
    right: 13px;
  }

  .menu__links {
    gap: 17px;
  }

  .menu__button {
    margin: 155px 0 0;
  }

  .menu__link {
    padding: 0 0 7px;
  }
}
